import {NgbDate} from '@ng-bootstrap/ng-bootstrap';

export function toNgbDate(date: Date | null): NgbDate {
  return !date ? null : new NgbDate(date.getFullYear(), date.getMonth() + 1, date.getDate());
}

export function toDate(value: NgbDate): Date {
  return !value ? null : new Date(value.year, value.month - 1, value.day);
}

export function isChronological(minDate: NgbDate, maxDate: NgbDate): boolean {
  return !(!!minDate && !!maxDate && minDate.after(maxDate));
}
