import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {PermissionsService} from './permissions.service';
import * as PermissionSnippets from './permissions.snippets';

@Component({
  selector: 'app-permissions',
  templateUrl: './permissions.component.html',
  styleUrls: ['./permissions.component.scss']
})
export class PermissionsComponent implements OnInit {
  public fruits: string[];
  public currentFruit: string;

  public isOperationUser: boolean;
  public isPayroller: boolean;

  public snippets = PermissionSnippets;

  constructor(
    private router: Router,
    private permissionsService: PermissionsService,
  ) {
  }

  public ngOnInit(): void {
    this.currentFruit = 'apple';
    this.fruits = ['apple', 'lemon', 'carrot'];
    this.updatePermissions();
  }

  public updatePermissions(): void {
    const codes = [this.currentFruit];
    if (this.isOperationUser) {
      codes.push('OPR0001');
    }
    if (this.isPayroller) {
      codes.push('PAY0001');
    }
    this.permissionsService.setPermissions(codes);
  }

}
