import {Pipe, PipeTransform} from '@angular/core';
import {formatNumber} from '@angular/common';

@Pipe({
  name: 'auiNumber',
})
export class NumberFormatterPipe implements PipeTransform {
  constructor() {}

  transform(value: number | string, decimalDigits: number): any {
    return format(value, decimalDigits);
  }
}

export function format(value: number | string, decimalDigits: number): string {
  if (value === null || value === undefined) {
    return null;
  }
  if (typeof value === 'string' || isNaN(Number(value))) {
    return value as string;
  }
  return formatNumber(value as number, 'fr-BE', '1.' + decimalDigits + '-' + decimalDigits);
}
