import {Component, forwardRef, Injector, Renderer2} from '@angular/core';
import {AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors} from '@angular/forms';
import {nationalRegistryNumberValidator} from './national-registry-number.validator';
import {NationalRegistryNumberFormatter} from 'acerta-datatypes';
import {TextComponent} from '../text/text.component';

@Component({
  selector: 'aui-national-registry-number',
  templateUrl: './national-registry-number.component.html',
  styleUrls: ['./national-registry-number.component.scss'],
  providers: [{
    provide: NG_VALIDATORS,
    useExisting: forwardRef(() => NationalRegistryNumberComponent),
    multi: true,
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => NationalRegistryNumberComponent),
    multi: true,
  }],
})
export class NationalRegistryNumberComponent extends TextComponent {
  constructor(
    renderer: Renderer2,
    injector: Injector,
  ) {
    super(renderer, injector);
  }

  public validate(control: AbstractControl): ValidationErrors | null {
    return nationalRegistryNumberValidator(control);
  }

  public onInput(): void {
    this.onChange(NationalRegistryNumberFormatter.parse(this.inputField.value));
  }

  public onBlur(): void {
    this.updateView(NationalRegistryNumberFormatter.parse(this.inputField.value));
    this.onTouch();
  }

  protected updateView(value: string): void {
    if (this.inputField) {
      this.renderer.setProperty(this.inputField, 'value', NationalRegistryNumberFormatter.format(value));
    }
  }
}
