import {Injectable} from '@angular/core';
import {NgbDatepickerI18n, NgbDateStruct} from '@ng-bootstrap/ng-bootstrap';
import {TranslateService} from '@ngx-translate/core';

@Injectable()
export class AcertaNgbDatepickerI18n extends NgbDatepickerI18n {
    constructor(private translateService: TranslateService) {
        super();
    }

    getDayAriaLabel(date: NgbDateStruct): string {
        return `${date.day}-${date.month}-${date.year}`;
    }

    getMonthFullName(month: number, year?: number): string {
        return this.getMonthShortName(month, year);
    }

    getMonthShortName(month: number, year?: number): string {
        return (this.translateService.instant('MONTHS') as string[])[month - 1];
    }

    getWeekdayShortName(weekday: number): string {
        return (this.translateService.instant('WEEKDAYS') as string[])[weekday - 1];
    }


}
