import {
  ElementRef,
  Injector,
  Input,
  OnChanges,
  OnInit,
  Renderer2,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import {AbstractControl, ControlValueAccessor, NgControl, ValidationErrors, Validator} from '@angular/forms';
import {touchedStrategy, ValidationStrategyFn} from '@acerta/ui/common';

export abstract class InputComponent implements OnInit, OnChanges, ControlValueAccessor, Validator {
  @ViewChild('inputFieldRef', {static: true}) inputFieldRef: ElementRef<HTMLInputElement>;

  @Input() id: string;
  @Input() placeholder = '';
  @Input() validationStrategy: ValidationStrategyFn = touchedStrategy;


  protected inputField: HTMLInputElement;
  protected onChange: (value: any) => {};
  protected onTouch: () => {};
  protected validatorChange: () => void;

  public ngControl: NgControl;
  public isDisabled = false;

  protected constructor(
    protected renderer: Renderer2,
    protected injector: Injector,
  ) {
  }

  ngOnInit(): void {
    this.ngControl = this.injector.get<NgControl>(NgControl as any);
    this.inputField = this.inputFieldRef.nativeElement;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.validatorChange) {
      this.validatorChange();
    }
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }

  registerOnValidatorChange(fn: () => void): void {
    this.validatorChange = fn;
  }

  writeValue(value: any): void {
    this.updateView(value);
  }

  setDisabledState(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  abstract validate(control: AbstractControl): ValidationErrors | null;

  public isInvalid(): boolean {
    return this.validationStrategy(this.ngControl);
  }

  public onInput(): void {
    this.onChange(this.inputField.value);
  }

  public onBlur(): void {
    this.updateView(this.inputField.value);
    this.onTouch();
  }

  protected updateView(value: string): void {
    if (this.inputField) {
      this.renderer.setProperty(this.inputField, 'value', value || '');
    }
  }
}

