import {AbstractControl, ValidationErrors} from '@angular/forms';
import {NationalRegistryNumberValidator, NationalRegistryNumberValidationState} from 'acerta-datatypes';

export function nationalRegistryNumberValidator(control: AbstractControl): ValidationErrors | null {
  if (!control || !control.value) {
    return null;
  }

  switch (NationalRegistryNumberValidator.validateNationalRegistryNumber(control.value)) {
    case NationalRegistryNumberValidationState.TOO_SHORT:
    case NationalRegistryNumberValidationState.TOO_LONG:
    case NationalRegistryNumberValidationState.BAD_CHARACTERS:
      return { nationalRegistryNumber: 'INVALID_FORMAT' };
    case NationalRegistryNumberValidationState.BAD_CHECKDIGITS:
      return { nationalRegistryNumber: 'INVALID_CHECK_DIGITS' };
    default:
      return null;
  }
}

