import {Component, forwardRef, Injector, Renderer2} from '@angular/core';
import {AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors} from '@angular/forms';
import {phoneNumberValidator} from './phone-number.validator';
import {TextComponent} from '../text/text.component';
import {PhoneNumberFormatter} from 'acerta-datatypes';

@Component({
  selector: 'aui-phone-number',
  templateUrl: './phone-number.component.html',
  styleUrls: ['./phone-number.component.scss'],
  providers: [{
    provide: NG_VALIDATORS,
    useExisting: forwardRef(() => PhoneNumberComponent),
    multi: true,
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => PhoneNumberComponent),
    multi: true,
  }],
})
export class PhoneNumberComponent extends TextComponent {
  constructor(
    renderer: Renderer2,
    injector: Injector
  ) {
    super(renderer, injector);
  }

  public onInput(): void {
    this.onChange(PhoneNumberFormatter.parse(this.inputField.value));
  }

  public onBlur(): void {
    this.updateView(PhoneNumberFormatter.parse(this.inputField.value));
    this.onTouch();
  }

  updateView(value: string): void {
    if (this.inputField) {
      this.renderer.setProperty(this.inputField, 'value', PhoneNumberFormatter.format(value));
    }
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return phoneNumberValidator(control);
  }
}
