import {Component, OnInit} from '@angular/core';
import * as DatepickerSnippets from './datepicker.snippets';

@Component({
  selector: 'app-datepicker',
  templateUrl: './datepicker.component.html',
  styleUrls: ['./datepicker.component.scss']
})
export class DatepickerComponent implements OnInit {
  public snippets = DatepickerSnippets;

  public model1: Date;
  public model2: Date;
  public model3: Date;
  public model4: Date;
  public model6: Date;
  public requiredModel: Date;
  public selectMinDateModel: Date;
  public selectMaxDateModel: Date;
  public minDateModel: Date;
  public maxDateModel: Date;
  public betweenDateModel: Date;
  public readonlyDate: Date = new Date();
  public disabledDays: Date[];
  public disabled = true;

  constructor() {
  }

  ngOnInit() {
      this.disabledDays = [5, 6, 7, 8, 9].map(d => {
          const date: Date = new Date();
          date.setDate(d);
          return date;
      });
  }

  resetModel6() {
      this.model6 = new Date();
  }
}
