import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-no-permission',
  templateUrl: './unauthorized.component.html',
  styleUrls: ['./unauthorized.component.scss']
})
export class UnauthorizedComponent implements OnInit {

  public gif: string;
  public gifList = [
    'https://i.giphy.com/media/nWUpWVZBTOwmI/giphy.gif',
    'https://i.giphy.com/media/3eKdC7REvgOt2/giphy.gif',
    'https://i.giphy.com/media/QUaqJRizED5NC/giphy.gif',
    'https://i.giphy.com/media/lSnmXEFSEY2sg/giphy.gif'
  ];

  constructor() {
  }

  private static getRandomElement(list: string[]) {
    return list[Math.floor(Math.random() * list.length)];
  }

  ngOnInit() {
    this.gif = UnauthorizedComponent.getRandomElement(this.gifList);
  }
}
