import {Component, OnInit, ViewChild} from '@angular/core';
import {NgForm} from '@angular/forms';
import {alwaysStrategy, dirtyStrategy, neverStrategy, touchedStrategy} from '@acerta/ui/common';
import * as FormSnippets from './forms.snippets';

@Component({
  selector: 'app-forms',
  templateUrl: './forms.component.html',
  styleUrls: ['./forms.component.scss']
})
export class FormsComponent implements OnInit {

  public snippets = FormSnippets;
  public isInvalid = false;
  public companyNumberWithStrategy: string;

  public alwaysStrategy = alwaysStrategy;
  public touchedStrategy = touchedStrategy;
  public dirtyStrategy = dirtyStrategy;
  public neverStrategy = neverStrategy;
  public validationStrategy = this.touchedStrategy;

  @ViewChild('dateValidationForm', { static: true }) public dateValidationForm: NgForm;
  public disabledDates = [new Date(2019, 11, 24)];
  public minDate = new Date();
  public inputModel = {
    acertaKey: '00000123456',
    startDate: new Date(2019, 0, 1),
  };

  constructor() { }

  ngOnInit() {
  }

  public scrollTo(id: string): void {
    document.getElementById(id).scrollIntoView();
  }

  bumpMinDate() {
    const date = new Date();
    date.setDate(this.minDate.getDate() + 1);
    this.minDate = date;
  }
}
