import {AbstractControl, ValidationErrors} from '@angular/forms';
import {IbanValidator, IbanValidationState} from 'acerta-datatypes';

export function ibanValidator(control: AbstractControl): ValidationErrors | null {
  if (!control || !control.value) {
    return null;
  }

  switch (IbanValidator.validateBankAccountNumber(control.value)) {
    case IbanValidationState.TOO_SHORT:
    case IbanValidationState.TOO_LONG:
    case IbanValidationState.BAD_FORMAT:
      return { iban: 'INVALID_FORMAT' };
    case IbanValidationState.BAD_CHECKDIGITS:
      return { iban: 'INVALID_CHECK_DIGITS' };
    default:
      return null;
  }
}

