import {Snippet} from '../../shared/snippet/snippet';

export const basicSnippet = Snippet({
  lang: 'html',
  code: `
<button type="button" tooltip="Your text." placement="bottom">
  Tooltip on bottom
</button>
`});

export const iconSnippet = Snippet({
  lang: 'html',
  code: `
<div class="form-group">
  <label for="street">Street
    <span class="tooltip-info" tooltip="Vul een straat in" placement="right">
      <i class="fa fa-info-circle text-secondary" aria-hidden="true"></i>
    </span>
  </label>
  <input id="street" type="text" placeholder="Diestsepoort" class="form-control">
</div>
`});

export const functionSnippet = Snippet({
  lang: 'typescript',
  code: `
@ViewChild('answer') private answer: TooltipDirective;
public showTooltip(): void {
  this.answer.show();
}
`});

export const functionTemplateSnippet = Snippet({
  lang: 'html',
  code: `
<i #answer="bs-tooltip" class="fa fa-question-circle text-secondary" tooltip="Cake!" placement="left"></i>
`});
