import {Component, forwardRef, Injector, Renderer2} from '@angular/core';
import {AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors} from '@angular/forms';
import {ibanValidator} from './iban.validator';
import {IbanFormatter} from 'acerta-datatypes';
import {TextComponent} from '../text/text.component';

@Component({
  selector: 'aui-iban',
  templateUrl: './iban.component.html',
  providers: [{
    provide: NG_VALIDATORS,
    useExisting: forwardRef(() => IbanComponent),
    multi: true,
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => IbanComponent),
    multi: true,
  }],
})
export class IbanComponent extends TextComponent {
  constructor(
    renderer: Renderer2,
    injector: Injector,
  ) {
    super(renderer, injector);
  }

  public validate(control: AbstractControl): ValidationErrors | null {
    return ibanValidator(control);
  }

  public onInput(): void {
    this.onChange(IbanFormatter.parse(this.inputField.value));
  }

  public onBlur(): void {
    this.updateView(IbanFormatter.parse(this.inputField.value));
    this.onTouch();
  }

  protected updateView(value: string): void {
    if (this.inputField) {
      this.renderer.setProperty(this.inputField, 'value', IbanFormatter.format(value));
    }
  }
}
