import {Component, AfterViewInit, ElementRef, ViewChild, Input, ChangeDetectionStrategy} from '@angular/core';

/*
Implementation based on https://github.com/ng-bootstrap/ng-bootstrap/tree/master/demo/src/app/shared/code
*/

import {ISnippet} from './snippet';
import {SnippetHighlightService} from './snippet-highlight.service';

@Component({
  selector: 'app-snippet',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <pre class="language-{{ snippet.lang }}"><code #code class="language-{{ snippet.lang }}"></code></pre>
  `
})
export class SnippetComponent implements AfterViewInit {

  @ViewChild('code', { static: true }) codeEl: ElementRef<HTMLElement>;

  @Input() snippet: ISnippet;

  constructor(private _service: SnippetHighlightService) { }

  ngAfterViewInit() {
    this.codeEl.nativeElement.innerHTML = this._service.highlight(this.snippet.code, this.snippet.lang);
  }
}
