import {Snippet} from '../../shared/snippet/snippet';

export const singleButton = Snippet({
  lang: 'html',
  code: `
<button id="btn-primary" (click)="doWork()" type="button" class="btn btn-primary">
  Primaire actie
</button>
`});

export const iconButton = Snippet({
  lang: 'html',
  code: `
<button id="btn-edit" (click)="edit()" type="button" class="btn btn-secondary">
  <i class="fa fa-fw fa-pen"></i>
</button>
`});

export const buttonsAligned = Snippet({
  lang: 'html',
  code: `
<div class="d-flex flex-row justify-content-end">
  <button id="btn-clear" (click)="clear()" type="button" class="btn aui-btn-link ml-2">Velden leegmaken</button>
  <button id="btn-search" (click)="search()" type="button" class="btn btn-primary ml-2">Zoeken</button>
</div>
`});

