import {AfterViewInit, Component, Input, OnInit} from '@angular/core';
import {AgGridAngular} from 'ag-grid-angular';
import {merge} from 'rxjs';

@Component({
  selector: 'aui-ag-grid-pagination',
  templateUrl: './ag-grid-pagination.component.html',
  styleUrls: ['./ag-grid-pagination.component.scss']
})
export class AgGridPaginationComponent implements OnInit, AfterViewInit {

  @Input() public agGrid: AgGridAngular;
  @Input() public pageSizes = [];

  public currentPage = 0;
  public totalPages = 0;

  constructor() { }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    if (!this.agGrid) {
      console.warn('aui-ag-grid-pagination > Referenced agGrid component not available.');
      return;
    }

    this.agGrid.suppressPaginationPanel = true;
    merge(this.agGrid.gridReady, this.agGrid.paginationChanged).subscribe(() => {
      this.updatePagination();
    });
  }

  updatePagination(): void {
    this.currentPage = this.agGrid.api.paginationGetCurrentPage() + 1;
    this.totalPages = this.agGrid.api.paginationGetTotalPages();
  }

  goFirst(): void {
    this.agGrid.api.paginationGoToFirstPage();
  }

  goPrevious(): void {
    this.agGrid.api.paginationGoToPreviousPage();
  }

  goNext(): void {
    this.agGrid.api.paginationGoToNextPage();
  }

  goLast(): void {
    this.agGrid.api.paginationGoToLastPage();
  }

  isFirst(): boolean {
    return this.currentPage === 1;
  }

  isLast(): boolean {
    return this.currentPage === this.totalPages;
  }

  changePageSize(pageSizeChangedEvent: Event): void {
    const newSize = parseInt((pageSizeChangedEvent.target as HTMLInputElement).value, 10);
    this.agGrid.api.paginationSetPageSize(newSize);
  }

  changePage(pageChangedEvent: Event): void {
    const newPage = parseInt((pageChangedEvent.target as HTMLInputElement).value, 10);
    this.agGrid.api.paginationGoToPage(newPage - 1);
  }
}
