import {Directive, ElementRef, forwardRef, HostListener, Renderer2} from '@angular/core';
import {AbstractControl, ControlValueAccessor, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validator} from '@angular/forms';
import {nationalRegistryNumberValidator} from './national-registry-number.validator';
import {NationalRegistryNumberFormatter} from 'acerta-datatypes';

@Directive({
  selector: '[auiNationalRegistryNumber][ngModel],[auiNationalRegistryNumber][formControlName],[auiNationalRegistryNumber][formControl]',
  providers: [{
    provide: NG_VALIDATORS,
    useExisting: forwardRef(() => NationalRegistryNumberDirective),
    multi: true
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => NationalRegistryNumberDirective),
    multi: true
  }]
})
export class NationalRegistryNumberDirective implements ControlValueAccessor, Validator {

  private _inputField: HTMLInputElement;
  private _onChange: (value: string) => {};
  private _onTouch: () => {};

  constructor(
    private renderer: Renderer2,
    private element: ElementRef,
  ) {
    this._inputField = this.element.nativeElement;
  }

  registerOnChange(fn: any): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this._onTouch = fn;
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return nationalRegistryNumberValidator(control);
  }

  writeValue(value: any): void {
    this.renderer.setProperty(this._inputField, 'value', NationalRegistryNumberFormatter.format(value));
  }

  @HostListener('input', ['$event.target.value'])
  input(value: string) {
    this._onChange(NationalRegistryNumberFormatter.parse(value));
  }

  @HostListener('blur')
  doOnBlur() {
    const parsedValue = NationalRegistryNumberFormatter.parse(this._inputField.value);
    const formattedValue = NationalRegistryNumberFormatter.format(parsedValue);
    this.renderer.setProperty(this._inputField, 'value', formattedValue);
    this._onTouch();
  }
}

