import {AfterViewInit, Component, ViewChild} from '@angular/core';
import {DialogModalType, DialogService, ModalComponent, SlideOutComponent} from '@acerta/ui';
import {ModalDirective, ModalOptions} from 'ngx-bootstrap';
import * as ModalSnippets from './modals.snippets';

@Component({
  selector: 'app-modals',
  templateUrl: './modals.component.html',
  styleUrls: ['./modals.component.scss']
})
export class ModalsComponent implements AfterViewInit {
  public snippets = ModalSnippets;

  @ViewChild('customModalConfig', {static: false}) customModalConfig: ModalComponent;
  @ViewChild('slideOut', {static: false}) slideOutComponent: SlideOutComponent;

  public someValue = 'String from demo component';
  public size: string;
  public side: string;
  public modalConfig: Partial<ModalOptions> = {
    ignoreBackdropClick: true,
    backdrop: false,
  };

  constructor(
    private dialogService: DialogService,
  ) {
  }

  ngAfterViewInit() {
    this.customModalConfig.modal.onShown.subscribe(($event: ModalDirective) => {
      console.log(`customModalConfig onShown event emitted: ${JSON.stringify({isShown: $event.isShown})}`);
    });
  }

  public openModal(modal: ModalComponent, modalConfig?: ModalOptions) {
    modal.modalConfig = Object.assign({}, modal.modalConfig, modalConfig);
    modal.show();
  }

  public openSlideOut(side: string, size: string) {
    this.size = size;
    this.side = side;
    this.slideOutComponent.show();
  }

  public openAlertDialog(): void {
    this.dialogService.showAlert('Something is up', 'An error occurred.. I think..');
  }

  public openConfirmationDialog(): void {
    this.dialogService.showConfirmation('Are you sure?', 'It really makes no difference.').result.subscribe((result: string) => {
      console.log(`User clicked ${result}.`);
    });
  }

  public openCustomConfigConfirmationDialog(): void {
    this.dialogService.showConfirmation('Are you sure?', 'Whoa wait! Where is the backdrop??',
      this.modalConfig).result.subscribe((result: string) => {
      console.log(`User clicked ${result}.`);
    });
  }

  public openInfoDialog(): void {
    this.dialogService.showDialog('Information', 'Now you know!', DialogModalType.INFO, [
      {btnAction: 'confirm', btnClass: 'btn-primary', btnText: 'BTN.OK'}
    ]).result.subscribe((result: string) => {
      console.log(`User clicked ${result}.`);
    });
  }
}
