import {Directive, ElementRef, forwardRef, HostListener, Renderer2} from '@angular/core';
import {AbstractControl, ControlValueAccessor, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validator} from '@angular/forms';
import {companyNumberValidator} from './company-number.validator';
import {CompanyNumberFormatter} from 'acerta-datatypes';

@Directive({
  selector: '[auiCompanyNumber][ngModel],[auiCompanyNumber][formControlName],[auiCompanyNumber][formControl]',
  providers: [{
    provide: NG_VALIDATORS,
    useExisting: forwardRef(() => CompanyNumberDirective),
    multi: true
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => CompanyNumberDirective),
    multi: true
  }]
})
export class CompanyNumberDirective implements ControlValueAccessor, Validator {

  private _inputField: HTMLInputElement;
  private _onChange: (value: string) => {};
  private _onTouch: () => {};

  constructor(private renderer: Renderer2, private element: ElementRef) {
    this._inputField = this.element.nativeElement;
  }

  registerOnChange(fn: any): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this._onTouch = fn;
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return companyNumberValidator(control);
  }

  writeValue(value: any): void {
    this.renderer.setProperty(this._inputField, 'value', CompanyNumberFormatter.format(value));
  }

  @HostListener('input', ['$event.target.value'])
  input(value: string) {
    const parsedValue = CompanyNumberFormatter.parse(value);
    this._onChange(parsedValue);
  }

  @HostListener('blur')
  doOnBlur() {
    const parsedValue = CompanyNumberFormatter.parse(this._inputField.value);
    this.renderer.setProperty(this._inputField, 'value', CompanyNumberFormatter.format(parsedValue));
    this._onTouch();
  }
}

