import {Snippet} from '../../shared/snippet/snippet';

export const serviceSnippet = Snippet({
  lang: 'typescript',
  code: `
import {NotificationService} from '@acerta/ui';

..

export class ExampleComponent {

  constructor(
    private notificationService: NotificationService,
  ) { }

  public showSuccess(): void {
    this.notificationService.success('I am a supernice success');
  }

}
`});

export const configurationSnippet = Snippet({
  lang: 'typescript',
  code: `
@NgModule({
  imports: [
      AcertaUIModule.forRoot({
        notificationsConfig: {
          tapToDismiss: false,
        },
      }),
  ],
})
export class CoreModule {
`});

export const singleConfigurationSnippet = Snippet({
  lang: 'typescript',
  code: `
    this.notificationService.error('translation.with.params', {
      tapToDismiss: false,
      translateParams: { key: value },
    });
`});
