import {AbstractControl, ValidationErrors, ValidatorFn} from '@angular/forms';
import {isNullOrWhitespace} from '@acerta/ui/common';

enum NumberValidationState {
  EMPTY = 0,
  VALID = 1,
  BAD_CHARACTERS = 5,
  MIN_VALUE_EXCEEDED = 6,
  MAX_VALUE_EXCEEDED = 7,
}

const validationStateMap = {
  0: null,
  1: null,
  5: 'INVALID_FORMAT',
  6: 'MIN_VALUE_EXCEEDED',
  7: 'MAX_VALUE_EXCEEDED',
};

export function numberValidator(minValue: number | null, maxValue: number | null): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (!control || control.value === null || control.value === undefined) {
      return null;
    }
    const value = control.value;
    const result = numberValidatorThatCouldBeExtractedToAGenericLibrary(value, minValue, maxValue);
    const mappedResult = validationStateMap[result];
    return mappedResult === null ? null : {number: mappedResult};
  };
}

export function numberValidatorThatCouldBeExtractedToAGenericLibrary(value: string | number,
                                                                     minValue: number | null,
                                                                     maxValue: number | null): NumberValidationState {

  if (isNullOrWhitespace('' + value)) {
    return NumberValidationState.EMPTY;
  }
  const valueAsNumber = Number(value);
  if (isNaN(valueAsNumber)) {
    return NumberValidationState.BAD_CHARACTERS;
  }
  if (minValue !== null && valueAsNumber < Number(minValue)) {
    return NumberValidationState.MIN_VALUE_EXCEEDED;
  }
  if (maxValue !== null && valueAsNumber > Number(maxValue)) {
    return NumberValidationState.MAX_VALUE_EXCEEDED;
  }
  return NumberValidationState.VALID;
}


