import {Component, OnInit} from '@angular/core';
import {CodStorage, CodCode, CodDefinition, CodResource} from '@acerta/ui';
import * as CodSnippets from './cod.snippets';

@Component({
  selector: 'app-cod',
  templateUrl: './cod.component.html',
  styleUrls: ['./cod.component.scss']
})
export class CodComponent implements OnInit {
  public snippets = CodSnippets;
  public definitionName: string;
  public codDefinition: CodDefinition;
  public codData: CodResource[];

  public dropdownModel: CodCode;

  constructor(
    private codStorage: CodStorage,
  ) {
  }

  public ngOnInit() {
    this.codStorage.findDefinition('BX3_CuroNationality').subscribe((result: CodDefinition) => {});
    this.definitionName = 'B71_Locatie';
  }

  public findDefinition(name: string): void {
    this.codStorage.findDefinition(name).subscribe((result: CodDefinition) => {
      this.codDefinition = result;
      this.codData = CodResource.buildArray(this.codDefinition, 'NL');
    });
  }
}
