import {AbstractControl, ValidationErrors} from '@angular/forms';
import {PhoneNumberValidator, PhoneNumberValidationState} from 'acerta-datatypes';

export function phoneNumberValidator(control: AbstractControl): ValidationErrors | null {
  if (!control || !control.value) {
    return null;
  }

  switch (PhoneNumberValidator.validatePhoneNumber(control.value)) {
    case PhoneNumberValidationState.TOO_SHORT:
      return { phoneNumber: 'TOO_SHORT'};
    case PhoneNumberValidationState.TOO_LONG:
      return { phoneNumber: 'TOO_LONG'};
    case PhoneNumberValidationState.BAD_CHARACTERS:
      return { phoneNumber: 'BAD_CHARACTERS'};
    case PhoneNumberValidationState.INVALID:
      return { phoneNumber: 'INVALID'};
    default:
      return null;
  }
}
