import {AbstractControl, ValidationErrors} from '@angular/forms';
import {EmailValidator, EmailValidationState} from 'acerta-datatypes';

export function emailValidator(control: AbstractControl): ValidationErrors | null {
  if (!control || !control.value) {
    return null;
  }

  switch (EmailValidator.validate(control.value)) {
    case EmailValidationState.EMPTY:
    case EmailValidationState.UNDETERMINED:
    case EmailValidationState.INVALID_MULTIPLE_AT:
    case EmailValidationState.INVALID_BADCHARACTER:
      return { email: 'INVALID_FORMAT' };
    case EmailValidationState.INVALID_DOMAIN:
    case EmailValidationState.INVALID_EMAIL:
      return { email: 'INVALID_EMAIL' };
    default:
      return null;
  }
}

