import {Snippet} from '../../shared/snippet/snippet';

export const customTemplate = Snippet({
  lang: 'html',
  code: `
<ng-template #customBody>
    <p>This is a custom body for this modal. Hooray!</p>
    <input class="form-control" [(ngModel)]="someValue">
</ng-template>

<ng-template #customFooter>
    <button type="button" class="btn btn-primary" (click)="hideCustomModal()">Custom OK</button>
</ng-template>

<aui-modal #customModalComponent
    headerTitle="Custom modal title"
    [headerTemplate]="..."
    [bodyTemplate]="customBody"
    [footerTemplate]="customFooter"
    (defaultDismiss)="hideCustomModal()"
    (defaultConfirm)="hideCustomModal()">
</aui-modal>
`});



export const configurationSnippet = Snippet({
  lang: 'typescript',
  code: `
@NgModule({
  imports: [
      AcertaUIModule.forRoot({
        notificationsConfig: {
          tapToDismiss: false,
        },
      }),
  ],
})
export class CoreModule {
`});

export const singleConfigurationSnippet = Snippet({
  lang: 'typescript',
  code: `
    this.notificationService.error('translation.with.params', {
      tapToDismiss: false,
      translateParams: { key: value },
    });
`});

export const slideOut = Snippet({
  lang: 'typescript',
  code: `
@ViewChild('slideOutComponent') slideOutComponent: SlideOutComponent;
..
public doSomething(): void {
  this.slideOutComponent.show();
}
`});

export const slideOutTemplate = Snippet({
  lang: 'html',
  code: `
<aui-slide-out id="demo-slide-out"
               #slideOutComponent
               [headerLabel]="'Nice header'"
               [size]="size"
               [side]="side"
               [primaryActionIsDisabled]="modalForm.invalid"
               (clickedClose)="hideSlideOut()"
               (clickedPrimaryAction)="hideSlideOut()"
               (clickedSecondaryAction)="hideSlideOut()">
  <!-- Add slideout content here -->
</aui-slide-out>
`});

export const dialogAlert = Snippet({
  lang: 'typescript',
  code: `
import {DialogService} from '@acerta/ui';
..

public openAlertDialog(): void {
  this.dialogService.showAlert('Something is up', 'An error occurred.. I think..');
}
`});

export const dialogConfirmation = Snippet({
  lang: 'typescript',
  code: `
import {DialogService} from '@acerta/ui';
..

public openConfirmationDialog(): void {
  this.dialogService.showConfirmation('Are you sure?', 'It really makes no difference.')
  .result.subscribe((result: string) => {
    console.log(\`User clicked \${result}.\`);
  });
}
`});

export const dialogInfo = Snippet({
  lang: 'typescript',
  code: `
import {DialogService} from '@acerta/ui';
..

public openInfoDialog(): void {
  this.dialogService.showDialog('Information', 'Now you know!', DialogModalType.INFO, [
    {btnAction: 'confirm', btnClass: 'btn-primary', btnText: 'BTN.OK'}
  ]).result.subscribe((result: string) => {
    console.log(\`User clicked \${result}.\`);
  });
}
`});

export const customConfigModalTs = Snippet({
  lang: 'typescript',
  code: `  public modalconfig: Partial<ModalOptions> = {
    ignoreBackdropClick: true,
    backdrop: false,
  };
`});

export const customConfigModalHtml = Snippet({
  lang: 'html',
  code: `  <aui-modal #myModalComponent
           headerTitle="Custom config"
           [bodyTemplate]="customBody"
           [modalConfig]="modalconfig"
           (defaultDismiss)="hideModal()"
           (defaultConfirm)="hideModal()">
</aui-modal>
`});

export const customConfigDialog = Snippet({
  lang: 'typescript',
  code: `  public modalconfig: Partial<ModalOptions> = {
    ignoreBackdropClick: true,
    backdrop: false,
  };
  public openConfirmationDialog(): void {
    this.dialogService.showConfirmation('Are you sure?', 'Whoa wait! Where is the backdrop??', this.modalconfig)
    .result.subscribe((result: string) => {
      console.log(\`User clicked \${result}.\`);
    });
  }
`});
