import {Snippet} from '../../shared/snippet/snippet';

export const checkboxExample = Snippet({
  lang: 'html',
  code: `
<div class="form-group row">
  <label class="col-md-4">Checkbox example:</label>
  <div class="col-md-8">
    <div class="custom-control custom-checkbox">
      <input class="custom-control-input" id="checkbox" type="checkbox">
      <label class="custom-control-label" for="checkbox">Check me</label>
    </div>
  </div>
</div>
`});

export const radioExample = Snippet({
  lang: 'html',
  code: `
<div class="form-group row">
  <label class="col-md-4">Radio button example:</label>
  <div class="col-md-8">
    <div class="custom-control custom-radio">
      <input class="custom-control-input" id="radio" type="radio">
      <label class="custom-control-label" for="radio">Radio star</label>
    </div>
  </div>
</div>
`});

export const formGroup = Snippet({
  lang: 'html',
  code: `
    <aui-form-group for="my_input_id"
                    label="translations.label"
                    required="true">
      <aui-text id="my_input_id"
                name="input name"
                [(ngModel)]="myInputModel"
                required>
      </aui-text>
    </aui-form-group>
`,
});
