import {Snippet} from '../../shared/snippet/snippet';

export const simpleTabs = Snippet({
  lang: 'html',
  code: `
    <div class="aui-tabs">
      <div class="aui-tab" [class.active]="selectedTab === 1" (click)="selectedTab = 1">
        <span class="aui-tab-content">
          Tabblad 1
        </span>
      </div>
      <div class="aui-tab" [class.active]="selectedTab === 2" (click)="selectedTab = 2">
        <span class="aui-tab-content">
          Tabblad 2
        </span>
      </div>
    </div>
`
});

export const dropdownTabs = Snippet({
  lang: 'html',
  code: `
    <div class="aui-tabs">
      <div class="aui-tab aui-dropdown">
        <div class="aui-tab-content" data-toggle="dropdown">
          <span class="dropdown-toggle">
            CategorieÃ«n
          </span>
          <div class="dropdown-menu">
            <span class="dropdown-item">Link 1</span>
            <span class="dropdown-item">Link 2</span>
            <div class="dropdown-divider"></div>
            <span class="dropdown-item">Link 3</span>
          </div>
        </div>
      </div>
    </div>
`
});
