import {Injectable} from '@angular/core';
import {NgxPermissionsService} from 'ngx-permissions';

@Injectable({
  providedIn: 'root'
})
export class PermissionsService {

  /*
   * This service would be responsible for loading the correct permission codes during initialization.
   * (presumably based on claims/authtokens or directly from iam+)
   */
  constructor(
    private permissionsService: NgxPermissionsService,
  ) {
  }

  public setPermissions(codes: string[]): void {
    this.permissionsService.loadPermissions(codes);
  }

  public hasPermission(code: string): Promise<boolean> {
    return this.permissionsService.hasPermission(code);
  }

}
