import {Component, OnInit} from '@angular/core';
import {DemoApiService} from './demo-api.service';
import * as DemoApiSnippets from './demo-api.snippets';
import {IDemoApiComment, IDemoApiPost} from './demo-api.model';

@Component({
  selector: 'app-demo-api',
  templateUrl: './demo-api.component.html',
  styleUrls: ['./demo-api.component.scss']
})
export class DemoApiComponent implements OnInit {

  public posts: IDemoApiPost[] = [];
  public comments: IDemoApiComment[] = [];
  public openedPost: IDemoApiPost;
  public newComment: string;
  public snippets = DemoApiSnippets;

  constructor(
    private demoApiService: DemoApiService,
  ) { }

  ngOnInit(): void {
    this.demoApiService.getPosts().subscribe(
      (data) => { this.posts = data; },
      (error) => { console.error(error); },
    );
  }

  public openPost(post: IDemoApiPost): void {
    this.demoApiService.getComments(post.id).subscribe((comments) => {
        this.openedPost = post;
        this.comments = comments;
      },
      (error) => { console.error(error); },
    );
  }

  public addComment(postId: number, body: string): void {
    this.demoApiService.addComment({postId, body}).subscribe((comment) => {
        this.newComment = '';
        this.comments.push(comment);
      },
      (error) => { console.error(error); },
    );
  }

  public triggerError(): void {
    this.demoApiService.triggerNotFoundError().subscribe(
      () => { /* do nothing */ },
      (error) => { console.error(error); },
    );
  }

  public triggerErrorWithFeedback(): void {
    this.demoApiService.triggerErrorWithFeedback().subscribe(
      () => { /* do nothing */ },
      (error) => { console.error(error); },
    );
  }
}
