import { Injectable } from '@angular/core';
import {Observable} from 'rxjs';
import {HttpService} from '@acerta/ui';
import {IDemoApiComment, IDemoApiPost} from './demo-api.model';
import {map} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DemoApiService {

  constructor(
    private httpService: HttpService,
  ) { }

  public getPosts(): Observable<IDemoApiPost[]> {
    return this.httpService.get<IDemoApiPost[]>('posts');
  }

  public getComments(postId: number): Observable<IDemoApiComment[]> {
    return this.httpService.get<IDemoApiComment[]>('comments').pipe(map(results => results.filter(c => c.postId === postId)));
  }

  public addComment(comment: IDemoApiComment): Observable<IDemoApiComment> {
    return this.httpService.post<IDemoApiComment, IDemoApiComment>('comments', comment);
  }

  public triggerNotFoundError(): Observable<any> {
    return this.httpService.get('non-existing-api-endpoint');
  }

  public triggerErrorWithFeedback(): Observable<any> {
    return this.httpService.get('employers');
  }

}
