import {Snippet} from '../../shared/snippet/snippet';

export const acertaKeyCode = Snippet({
  lang: 'html',
  code: `
<aui-form-group for="acertaKey"
                label="labels.acertaKey"
                required="true">
    <aui-acerta-key id="acertaKey"
                    name="acertaKey"
                    placeholder="labels.acertaKey"
                    [(ngModel)]="acertaKey"
                    required>
    </aui-acerta-key>
</aui-form-group>
`,
});

export const acertaKeyPipe = Snippet({
  lang: 'html',
  code: `
{{acertaKey}} => {{acertaKey | auiAcertaKey}}
`,
});

export const companyNumberCode = Snippet({
  lang: 'html',
  code: `
<aui-form-group label="BUSINESS_TYPES.COMPANY_NUMBER_COMPONENT_LABEL"
                for="company_number_1" required="true">
    <aui-company-number id="company_number_1"
                        name="company_number_1"
                        placeholder="BUSINESS_TYPES.COMPANY_NUMBER_COMPONENT_LABEL"
                        [(ngModel)]="companyNumber1"
                        required>
    </aui-company-number>
</aui-form-group>`
});

