export const currencyComponent = Snippet({
  lang: 'html',
  code: `
<aui-form-group for="currency"
                label="labels.currency">
    <aui-currency id="currency"
                  name="currency"
                  placeholder="labels.amount"
                  [symbol]="'â¬'"
                  [minValue]="-1000"
                  [maxValue]="1000"
                  [(ngModel)]="currency">
    </aui-currency>
</aui-form-group>
`,
});

export const currencyPipes = Snippet({
  lang: 'html',
  code: `
<aui-form-group for="currency"
                label="labels.currency">
  {{currency | auiCurrency}}
</aui-form-group>
<aui-form-group for="currency"
                label="labels.currency">
  {{currency | auiCurrency:0:'$'}}
</aui-form-group>
<aui-form-group for="currency"
                label="labels.currency">
  {{currency | auiCurrency:0:''}}
</aui-form-group>
`,
});

