export const numberWithDecimals = Snippet({
  lang: 'html',
  code: `
<aui-form-group for="number"
                label="labels.number"
                required="true">
    <aui-number id="number"
                name="number"
                placeholder="labels.amount"
                [(ngModel)]="number1"
                [decimalDigits]=2
                required>
    </aui-number>
</aui-form-group>
`
});

export const numberWithButtons = Snippet({
  lang: 'html',
  code: `
<aui-form-group for="number"
                label="labels.number"
                required="true">
    <div class="w-25">
        <aui-number id="number"
                name="number"
                [(ngModel)]="number"
                [minValue]=0
                [maxValue]=9999
                [showButtons]=true
                required>
        </aui-number>
    </div>
</aui-form-group>
`
});

export const textCode = Snippet({
  lang: 'html',
  code: `
<aui-form-group for="textInput"
                label="labels.text"
                required="true">
  <aui-text id="textInput"
            name="textInput"
            placeholder="vul hier iets in"
            [(ngModel)]="textInput"
            [minlength]="2"
            [maxlength]="5"
            disabled="false"
            required>
  </aui-text>
</aui-form-group>
`,
});

export const textAreaCode = Snippet({
  lang: 'html',
  code: `
<aui-form-group label="BUSINESS_TYPES.TEXT_COMPONENT_LABEL"
                for="text_1"
                required="true">
    <aui-textarea id="textarea_1"
                  class="large"
                  name="text_1"
                  placeholder="BUSINESS_TYPES.TEXT_COMPONENT_LABEL"
                  [(ngModel)]="text1"
                  [minlength]="2"
                  [maxlength]="5"
                  [nbOfLines]="5"
                  [resizable]="true"
                  required>
    </aui-textarea>
</aui-form-group>
`,
});

