import {Component, OnInit} from '@angular/core';
import {NotificationService} from '@acerta/ui';
import * as NotificationSnippets from './notifications.snippets';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.scss']
})
export class NotificationsComponent implements OnInit {
  public snippets = NotificationSnippets;

  constructor(private notificationService: NotificationService) { }

  ngOnInit() {

  }

  public warning(): void {
    this.notificationService.warning('NOTIFICATIONS.WARNING', {
      title: 'I am a warning title',
      translateParams: {aParam: '(!!!!!)'},
    });
  }

  public info(): void {
    this.notificationService.info('I am <span class="font-weight-bold">some nice</span>  info');
  }

  public success(): void {
    this.notificationService.success('I am a supernice success');
  }

  public error(): void {
    this.notificationService.error('I am an awful error', {
      closeButton: true,
      extendedTimeOut: 0,
      tapToDismiss: false,
      timeOut: 0,
    });
  }
}
