import {Component, forwardRef,  Injector, Renderer2} from '@angular/core';
import {AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors} from '@angular/forms';
import {alwaysStrategy} from '@acerta/ui/common';
import {InputComponent} from '../input/input.component';

@Component({
  selector: 'aui-text',
  templateUrl: './text.component.html',
  styleUrls: ['./text.component.scss'],
  providers: [{
    provide: NG_VALIDATORS,
    useExisting: forwardRef(() => TextComponent),
    multi: true,
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => TextComponent),
    multi: true,
  }],
})
export class TextComponent extends InputComponent {
  constructor(
    renderer: Renderer2,
    injector: Injector,
  ) {
    super(renderer, injector);
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return null;
  }

  public isInvalid(): boolean {
    if (this.ngControl.hasError('maxlength')) {
      return alwaysStrategy(this.ngControl);
    }
    return super.isInvalid();
  }
}

