import {AbstractControl, ValidationErrors} from '@angular/forms';
import {CompanyNumberValidator, CompanyNumberValidationState} from 'acerta-datatypes';

export function companyNumberValidator(control: AbstractControl): ValidationErrors | null {
  if (!control || !control.value) {
    return null;
  }

  switch (CompanyNumberValidator.validateCompanyNumber(control.value)) {
    case CompanyNumberValidationState.TOO_SHORT:
    case CompanyNumberValidationState.TOO_LONG:
    case CompanyNumberValidationState.BAD_CHARACTERS:
      return { companyNumber: 'INVALID_FORMAT' };
    case CompanyNumberValidationState.BAD_CHECKDIGITS:
      return { companyNumber: 'INVALID_CHECK_DIGITS' };
    default:
      return null;
  }
}
