import {Snippet} from '../../shared/snippet/snippet';

export const basicSnippet = Snippet({
  lang: 'html',
  code: `
<aui-autocomplete id="ac1"
                  name="ac1"
                  label="Land"
                  [(ngModel)]="ac1"
                  [typeaheadSource]="countries"
                  noResultsLabel="Er werden geen landen gevonden."
                  required>
</aui-autocomplete>
`
});

export const asyncSnippet = Snippet({
  lang: 'html',
  code: `
<aui-autocomplete id="ac2"
                  name="ac2"
                  label="Land"
                  [(ngModel)]="ac2"
                  typeaheadSource="demo/countries"
                  [typeaheadWaitMs]="500"
                  [typeaheadMinLength]="3">
</aui-autocomplete>
`});

export const objectSnippet = Snippet({
  lang: 'html',
  code: `
<aui-autocomplete id="ac3"
                  name="ac3"
                  label="Land"
                  [(ngModel)]="ac3"
                  typeaheadSource="demo/country-objects"
                  [typeaheadAsync]="true"
                  [typeaheadWaitMs]="500"
                  [typeaheadMinLength]="3"
                  typeaheadOptionField="name">
</aui-autocomplete>
`});

export const serverSnippet = Snippet({
  lang: 'html',
  code: `
<aui-autocomplete id="ac4"
                  name="ac4"
                  label="Land"
                  [(ngModel)]="ac4"
                  typeaheadSource="demo/countries-param"
                  typeaheadServerProcessing="true"
                  [typeaheadAsync]="true"
                  [typeaheadWaitMs]="500"
                  [typeaheadMinLength]="3">
</aui-autocomplete>
`});

export const serverObjectsSnippet = Snippet({
  lang: 'html',
  code: `
<aui-autocomplete id="ac5"
                  name="ac5"
                  label="Land"
                  [(ngModel)]="ac5"
                  typeaheadSource="demo/country-objects-param"
                  typeaheadServerProcessing="true"
                  [typeaheadAsync]="true"
                  [typeaheadWaitMs]="500"
                  [typeaheadMinLength]="3"
                  typeaheadOptionField="name">
</aui-autocomplete>
`});
