import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'auiNumberParser'
})
export class NumberParserPipe implements PipeTransform {
  transform(value: string, decimalDigits: number): number | string {
    return parse(value, decimalDigits);
  }
}

export function parse(value: string, decimalDigits: number): number | string {
  if (!value || !value.trim()) {
    return null;
  }
  const valueWithPoint = value.replace(/,/g, '.');
  const valueWithPointAndWithoutBlanks = valueWithPoint.replace(/\s+/g, '');
  const valueAsNumber = Number(valueWithPointAndWithoutBlanks);
  if (isNaN(valueAsNumber)) {
    return value;
  }
  return parseFloat(valueAsNumber.toFixed(decimalDigits));
}
