import {Snippet} from '../../shared/snippet/snippet';

export const permissionGuard = Snippet({
  lang: 'typescript',
  code: `
import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { PermissionsService } from '../permissions.service';

@Injectable({ providedIn: 'root' })
export class LemonGuard implements CanActivate {
  constructor(private router: Router, private permissionsService: PermissionsService) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<boolean> {
    return this.permissionsService.hasPermission('lemon').then((permitted: boolean) => {
      if (!permitted) {
        this.router.navigate(['/demo/permissions/unauthorized']);
      }
      return permitted;
    });
  }
}
`});

export const permissionRoutes = Snippet({
  lang: 'typescript',
  code: `
{
    path: 'permissions-lemon',
    component: LemonComponent,
    canActivate: [LemonGuard]
},
`});

export const permissionDirectives = Snippet({
  lang: 'html',
  code: `
<p *ngxPermissionsOnly="['OPR0001']">
  Deze tekst zie je als OPR medewerker
</p>
<p *ngxPermissionsOnly="['PAY0001']">
  Deze tekst zie je als Payroller
</p>
<p *ngxPermissionsExcept="['OPR0001', 'PAY0001']">
  Awwww.. Je hebt geen van beide!
</p>
`});


export const permissionModule = Snippet({
  lang: 'typescript',
  code: `
import {NgxPermissionsModule} from 'ngx-permissions';

@NgModule({
  ..
  imports: [
    ...
    NgxPermissionsModule.forRoot(),
  ],
  exports: [
    ...
    NgxPermissionsModule,
  ],
})
export class CoreModule { }
`});
