import {Snippet} from '../../shared/snippet/snippet';

export const apiServiceSnippet = Snippet({
  lang: 'typescript',
  code: `
import {HttpService} from '@acerta/ui';

@Injectable({
  providedIn: 'root'
})
export class BlogPostService {

  constructor(
    private httpService: HttpService,
  ) { }

  public getPosts(): Observable<Post[]> {
    return this.httpService.get<Post[]>('posts');
  }
}
`});

export const httpServiceSnippet = Snippet({
  lang: 'typescript',
  code: `
import {HttpService} from '@acerta/ui';

..

public getPosts(authorId: string): Observable<Post[]> {
  // Add query parameters (only strings/arrays) to URL. (e.g. will request "/posts?author=12")
  return this.httpService.get<Post[]>('posts', {
    params: {
        author: authorId,
    },
  });
}
`});

export const componentSnippet = Snippet({
  lang: 'typescript',
  code: `
ngOnInit(): void {
  this.blogService.getPosts().subscribe(
    (data) => { this.posts = data; },
    (error) => { /* handle exceptional flow */ },
  );
}
`});

