import {Snippet} from '../../shared/snippet/snippet';

export const cssThemeSnippet = Snippet({
  lang: 'css',
  code: `
ag-grid-angular {
  @extend .ag-theme-bootstrap;
}
`});

export const tableTemplate = Snippet({
  lang: 'html',
  code: `
<table class="table table-sm">
  <thead>
  <tr>
    <th>Make</th>
    <th>Model</th>
    <th>Price</th>
  </tr>
  </thead>
  <tbody>
    <tr *ngFor="let row of rowData">
      <td>{{row.make}}</td>
      <td>{{row.model}}</td>
      <td>{{row.price}}</td>
    </tr>
  </tbody>
</table>
`});

export const basicTemplate = Snippet({
  lang: 'html',
  code: `
<ag-grid-angular
  #agGrid
  style="width: 100%;height: 550px"
  class="ag-theme-bootstrap"
  [columnDefs]="columnDefs"
  [rowData]="rowData"
  (rowClicked)="onRowClick($event)">
</ag-grid-angular>
`});

export const basicScript = Snippet({
  lang: 'typescript',
  code: `
import {AgGridAngular} from 'ag-grid-angular';
..

@ViewChild('agGrid') agGrid: AgGridAngular;

public columnDefs = [
    { headerName: 'Make', field: 'make', sortable: true },
    { headerName: 'Model', field: 'model', sortable: true },
    { headerName: 'Price', field: 'price', sortable: true },
];
public rowData = [ ... ];

public ngOnInit(): void {
  ..
  this.agGrid.gridReady.subscribe(() => {
    grid.api.sizeColumnsToFit();
  });
}
`
});

export const paginationTemplate = Snippet({
  lang: 'html',
  code: `
    <ag-grid-angular #agGrid ...></ag-grid-angular>
    <aui-ag-grid-pagination
      class="d-flex flex-row justify-content-center mt-2"
      [agGrid]="agGrid"
      [pageSizes]="[15, 25, 50]">
    </aui-ag-grid-pagination>
`});

export const paginationScript = Snippet({
  lang: 'typescript',
  code: `
import {AgGridAngular} from 'ag-grid-angular';
..

@ViewChild('agGrid') agGrid: AgGridAngular;

public ngOnInit(): void {
  ..
  this.agGrid.paginationPageSize = 15; // Value should be present in pageSizes array.
}
`
});
