import {Component, forwardRef, Injector, Renderer2} from '@angular/core';
import {AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors} from '@angular/forms';
import {companyNumberValidator} from './company-number.validator';
import {CompanyNumberFormatter} from 'acerta-datatypes';
import {TextComponent} from '../text/text.component';

@Component({
  selector: 'aui-company-number',
  templateUrl: './company-number.component.html',
  styleUrls: ['./company-number.component.scss'],
  providers: [{
    provide: NG_VALIDATORS,
    useExisting: forwardRef(() => CompanyNumberComponent),
    multi: true,
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => CompanyNumberComponent),
    multi: true,
  }],
})
export class CompanyNumberComponent extends TextComponent {
  constructor(
    renderer: Renderer2,
    injector: Injector,
  ) {
    super(renderer, injector);
  }

  public onInput(): void {
    this.onChange(CompanyNumberFormatter.parse(this.inputField.value));
  }

  public onBlur(): void {
    this.updateView(CompanyNumberFormatter.parse(this.inputField.value));
    this.onTouch();
  }

  updateView(value: string): void {
    if (this.inputField) {
      this.renderer.setProperty(this.inputField, 'value', CompanyNumberFormatter.format(value));
    }
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return companyNumberValidator(control);
  }
}
