import {MenuItem} from '@acerta/ui';

export const footerhtml = `
 <div class="action-bar-container d-flex flex-column flex-grow-1 my-md-5">
    <div class="content-bar bg-white px-md-5 py-5">
      <div class="container px-md-0 d-flex flex-row flex-grow-1">
      your content here
      </div>
    </div>
    <div class="action-bar py-3 bg-white">
      <div class="container to-fluid-down-md px-md-0 d-flex h-100">
        <div class="d-flex w-100 align-items-center">
          <button id="leftLink"
                  class="btn aui-btn-link">
            Annuleren
          </button>
          <button id="rightButton"
                  class="btn btn-primary ml-auto">
            Goedkeuren
          </button>
        </div>
      </div>
    </div>
  </div>

`;

export const footercss = `

@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins/hover";
@import "~bootstrap/scss/mixins/breakpoints";


.action-bar {
  border-top: 2px solid acerta-color('grey-light');
}

.action-bar-container {
  height: 400px;
  border: 4px solid acerta-color('grey-light');
}


.content-bar {
  overflow-y: scroll;
}
`;


export const headers = `
<aui-mobile-menu [applicationName]="'APP_NAME'"
                     [menuItems]="menuItems"
                     [username]="username"
                     class="d-md-none"
                     (logoClicked)="onLogoClicked()">
</aui-mobile-menu>
<aui-desktop-header [applicationName]="'APP_NAME'"
                    [username]="username"
                    [fullScreen]="false"
                    (logoClicked)="onLogoClicked()"
                    [showDisclaimer]="true"
                    class="d-none d-md-block">
</aui-desktop-header>
<aui-desktop-navbar [menuItems]="menuItems"
                    [fullScreen]="false"
                    class="d-none d-md-block">
</aui-desktop-navbar>
`;

export const menuItems = `
  menuItems = [
    new MenuItem({
      label: 'Elementen', iconClass: 'fa fa-th', children: [
        new MenuItem({label: 'Componenten', iconClass: 'fa fa-shapes', route: '/demo/forms'}),
        new MenuItem({label: 'Services', iconClass: 'fa fa-wrench', route: '/demo/notifications'}),
      ]
    }),
    new MenuItem({
      label: 'Layouts', iconClass: 'fas fa-desktop', children: [
        new MenuItem({ label: 'AcertApp', route: '/layout/acertapp' }),
        new MenuItem({ label: 'LCI Search', route: '/layout/lci-search' }),
        new MenuItem({ label: 'Zelfstandigen', route: '/layout/zelfstandigen' }),
        new MenuItem({ label: 'Bedrijfsleiders', route: '/layout/bedrijfsleiders' }),
      ]
    }),
    new MenuItem({label: 'Over Acerta UI', route: '/about', iconClass: 'fa fa-info-circle', desktopHideIcon: true}),
    new MenuItem({label: 'Contacteer ons', route: '/contact', iconClass: 'far fa-comment-alt'}),
  ];
  `;

export const disclaimerFooter = `
  <aui-mobile-footer class="d-md-none"></aui-mobile-footer>
  `;
