import {AfterViewInit, ChangeDetectorRef, Component, forwardRef, Injector, Input, Renderer2} from '@angular/core';
import {AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors} from '@angular/forms';
import {format} from './number-formatter.pipe';
import {parse} from './number-parser.pipe';
import {numberValidator} from './number.validator';
import {InputComponent} from '../input/input.component';

@Component({
  selector: 'aui-number',
  templateUrl: './number.component.html',
  styleUrls: ['./number.component.scss'],
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => NumberComponent),
      multi: true,
    },
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => NumberComponent),
      multi: true,
    },
  ],
})
export class NumberComponent extends InputComponent implements AfterViewInit {
  @Input() decimalDigits = 0;
  @Input() minValue = -1000000000000;
  @Input() maxValue = 1000000000000;
  @Input() showButtons = false;

  constructor(
    renderer: Renderer2,
    injector: Injector,
    private cdRef: ChangeDetectorRef,
  ) {
    super(renderer, injector);
  }

  ngAfterViewInit(): void {
    this.cdRef.detectChanges();
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return numberValidator(this.minValue, this.maxValue)(control);
  }

  public onInput(): void {
    const parsedValue = parse(this.inputField.value, this.decimalDigits);
    this.onChange(typeof parsedValue === 'number' ? parsedValue : this.inputField.value);
  }

  public onBlur(): void {
    const parsedValue = parse(this.inputField.value, this.decimalDigits);
    this.updateView(typeof parsedValue === 'number' ? parsedValue : this.inputField.value);
    this.onTouch();
  }

  public subtract(): void {
    const value = parse(this.inputField.value, this.decimalDigits) || 0;
    if (typeof value === 'number') {
      this.updateValue(Math.min(value - 1, this.maxValue));
    } else if (value === null) {
      this.updateValue(-1);
    }
  }

  public add(): void {
    const value = parse(this.inputField.value, this.decimalDigits) || 0;
    if (typeof value === 'number') {
      this.updateValue(Math.max(value + 1, this.minValue));
    } else if (value === null) {
      this.updateValue(1);
    }
  }

  public canSubtract(): boolean {
    const value = parse(this.inputField.value, this.decimalDigits);
    return (value === null && this.minValue <= -1) || (typeof value === 'number' && value > this.minValue);
  }

  public canAdd(): boolean {
    const value = parse(this.inputField.value, this.decimalDigits);
    return (value === null && this.maxValue >= 1) || (typeof value === 'number' && value < this.maxValue);
  }

  public updateView(value: string | number): void {
    if (this.inputField) {
      this.renderer.setProperty(this.inputField, 'value', format(value, this.decimalDigits));
    }
  }

  private updateValue(newValue: number) {
    this.updateView(newValue);
    this.onChange(newValue);
  }
}
