import {Snippet} from '../../shared/snippet/snippet';

export const codInit = Snippet({
  lang: 'typescript',
  code: `
const routes: Routes = [
  {
    path: 'cod',
    component: CodComponent,
    resolve: [CodInitResolve],
    data: {
      cod: {
        definitions: ['B71_Locatie'],
      },
    }
  },
];
`
});

export const codSelect = Snippet({
  lang: 'html',
  code: `
<aui-form-group label="Cod nationalities" for="nationality" required="true">
  <aui-cod-select id="nationality"
                  name="nationality"
                  [(ngModel)]="dropdownModel"
                  codName="BX3_CuroNationality"
                  [isDefinition]="true"
                  [isSorted]="true"
                  [emptyOption]="true"
                  required>
  </aui-cod-select>
</aui-form-group>
`
});
