import {AbstractControl, ValidationErrors} from '@angular/forms';
import {AcertaKeyValidator, AcertaKeyValidationState} from 'acerta-datatypes';

export function acertaKeyValidator(control: AbstractControl): ValidationErrors | null {
  if (!control || !control.value) {
    return null;
  }

  switch (AcertaKeyValidator.validateAcertaKey(control.value)) {
    case AcertaKeyValidationState.TOO_LONG:
    case AcertaKeyValidationState.BAD_CHARACTERS:
      return {acertaKey: 'INVALID_FORMAT'};
    default:
      return null;
  }
}
