import {Component, OnInit, ViewChild} from '@angular/core';
import * as GridSnippets from './grids.snippets';
import {AgGridAngular} from 'ag-grid-angular';

@Component({
  selector: 'app-grid',
  templateUrl: './grids.component.html',
  styleUrls: ['./grids.component.scss']
})
export class GridsComponent implements OnInit {

  @ViewChild('agGrid', { static: true }) agGrid: AgGridAngular;
  @ViewChild('agGridPagination', { static: true }) agGridPagination: AgGridAngular;

  public log: string[] = [];
  public columnDefs: any[];
  public rowData: { make: string; model: string; price: number }[];
  public snippets = GridSnippets;

  constructor(
  ) {
  }

  public ngOnInit(): void {
    this.columnDefs = [
      { headerName: 'Make', field: 'make', resizable: true, sortable: true },
      { headerName: 'Model', field: 'model', sortable: true },
      { headerName: 'Price', field: 'price', sortable: true },
    ];
    this.rowData = [
      { make: 'Toyota', model: 'Celica', price: 35000 },
      { make: 'Nissan', model: 'Mondeo', price: 32000 },
      { make: 'Mercedes', model: 'A Class', price: 72000 },
      { make: 'Toyota', model: 'Celica', price: 35000 },
      { make: 'BMW', model: '3 Series', price: 32000 },
      { make: 'Porsche', model: 'Boxter', price: 72000 },
      { make: 'Toyota', model: 'Celica', price: 35000 },
      { make: 'Ford', model: 'Mondeo', price: 32000 },
      { make: 'BMW', model: '3 Series', price: 72000 },
      { make: 'Toyota', model: 'Celica', price: 35000 },
      { make: 'Ford', model: 'Mondeo', price: 32000 },
      { make: 'Audi', model: 'A4', price: 35000 },
      { make: 'Mercedes', model: 'A Class', price: 32000 },
      { make: 'Nissan', model: 'Boxter', price: 72000 },
      { make: 'Toyota', model: 'Celica', price: 35000 },
      { make: 'Ford', model: 'Mondeo', price: 32000 },
      { make: 'Porsche', model: 'Boxter', price: 72000 },
      { make: 'Toyota', model: 'Celica', price: 35000 },
      { make: 'Audi', model: 'A4', price: 32000 },
      { make: 'VW', model: 'Boxter', price: 72000 },
      { make: 'BMW', model: '3 Series', price: 35000 },
      { make: 'VW', model: 'Mondeo', price: 32000 },
      { make: 'Porsche', model: 'Boxter', price: 72000 },
      { make: 'Toyota', model: 'Celica', price: 35000 },
      { make: 'Mercedes', model: 'A Class', price: 32000 },
      { make: 'Porsche', model: 'Boxter', price: 72000 },
    ];

    this.prepareGrid(this.agGrid);
    this.prepareGrid(this.agGridPagination);
  }

  private prepareGrid(grid: AgGridAngular) {
    grid.rowHeight = 30;
    grid.headerHeight = 30;
    grid.paginationPageSize = 15; // Value should be present in pageSizes array.
    grid.gridReady.subscribe(() => {
      grid.api.sizeColumnsToFit();
    });
  }

  public onRowClick($event: any): void {
    this.log.push(`you clicked row: ${JSON.stringify($event.data)}`);
  }
}
