import {Component, EventEmitter, forwardRef, Injector, Input, OnChanges, OnInit, Output, SimpleChanges, TemplateRef} from '@angular/core';
import {
  AbstractControl,
  ControlValueAccessor,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  NgControl,
  ValidationErrors,
  Validator
} from '@angular/forms';
import {touchedStrategy, ValidationStrategyFn} from '@acerta/ui/common';
import {TypeaheadMatch} from 'ngx-bootstrap';
import {Observable, of} from 'rxjs';
import {catchError, mergeMap} from 'rxjs/operators';
import {HttpService} from '../../core/http/http.service';

@Component({
  selector: 'aui-autocomplete',
  templateUrl: './autocomplete.component.html',
  styleUrls: ['./autocomplete.component.scss'],
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => AutocompleteComponent),
      multi: true
    },
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => AutocompleteComponent),
      multi: true
    },
  ]
})
export class AutocompleteComponent implements OnInit, OnChanges, ControlValueAccessor, Validator {
  @Input() id: string;
  @Input() name: string;
  @Input() placeholder = '';
  @Input() validationStrategy: ValidationStrategyFn = touchedStrategy;
  isDisabled = false;

  @Input() set typeaheadSource(value: string | any) {
    this._source = value;
    if (this.isInitialized) {
      this.setTypeaheadSource(value);
    }
  }

  @Input() typeaheadServerProcessing = false;
  @Input() noResultsLabel = 'AUTOCOMPLETE.NO_RESULTS_FOUND';

  @Output() typeaheadLoading: EventEmitter<boolean> = new EventEmitter();
  @Output() typeaheadNoResults: EventEmitter<boolean> = new EventEmitter();
  @Output() typeaheadOnBlur: EventEmitter<TypeaheadMatch> = new EventEmitter();
  @Output() typeaheadOnSelect: EventEmitter<TypeaheadMatch> = new EventEmitter();

  // ngx-bootstrap inputs
  @Input() container: string;
  @Input() dropup: boolean;
  @Input() optionsListTemplate: TemplateRef<any>;
  @Input() typeaheadAsync: boolean;
  @Input() typeaheadGroupField: string;
  @Input() typeaheadHideResultsOnBlur: boolean;
  @Input() typeaheadItemTemplate: TemplateRef<any>;
  @Input() typeaheadLatinize: boolean;
  @Input() typeaheadMinLength = 3;
  @Input() typeaheadOptionField: string;
  @Input() typeaheadOptionsInScrollableView: number;
  @Input() typeaheadOptionsLimit: number;
  @Input() typeaheadPhraseDelimiters: string;
  @Input() typeaheadScrollable = false;
  @Input() typeaheadSelectFirstItem = true;
  @Input() typeaheadSingleWords = true;
  @Input() typeaheadWaitMs = 500;
  @Input() typeaheadWordDelimiters: string;

  private _onChange: (value: any) => {};
  private _onTouch: () => {};
  private _validatorChange: () => void;
  private _source: string | any;
  private isInitialized = false;

  public innerSource: Observable<any> | any;
  public httpResponse: any[];
  public isLoading = false;
  public ngControl: NgControl;
  public model: any;
  public hasNoResults: boolean;

  constructor(private _injector: Injector, private httpService: HttpService) {
  }

  ngOnInit() {
    this.ngControl = this._injector.get<NgControl>(NgControl as any);
    this.setTypeaheadSource(this._source);
    this.isInitialized = true;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this._validatorChange) {
      this._validatorChange();
    }
  }

  public setDisabledState(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  private setTypeaheadSource(value: string | any) {
    if (value == null) {
      this.innerSource = of(null);
      return;
    }

    if (typeof value === 'string') {
      // do http call
      const url = value;
      const matchProperty = this.typeaheadOptionField;

      if (this.typeaheadServerProcessing) {
        // server does processing, so return result
        this.innerSource = this.createObservableForServerProcessing(url);
      } else {
        // server does no processing, so doing it manually
        this.innerSource = this.createObservableForManualProcessing(url, matchProperty);
      }
    } else {
      this.innerSource = value;
    }
  }

  private createObservableForServerProcessing(url: string): Observable<any> {
    return new Observable((observer: any) => {
      observer.next(this.model);
    }).pipe(
      mergeMap((search: string) => {
        const params = {query: search};
        return this.httpService.get<any>(url, {params}, {hideErrorMessage: true, hideSpinner: true}).pipe(
          catchError(() => {
            this.isLoading = false;
            return of(null);
          })
        );
      })
    );
  }

  private createObservableForManualProcessing(url: string, matchProperty: string): Observable<any> {
    this.httpService.get<any>(url).subscribe(result => this.httpResponse = result);
    return new Observable((observer: any) => {
      observer.next(this.model);
    }).pipe(
      mergeMap((search: string) => this.getFilteredServerResults(search, matchProperty))
    );
  }

  private getFilteredServerResults(search: string, matchProperty: string): Observable<any> {
    const query = new RegExp(search, 'i');
    if (this.httpResponse) {
      return of(
        this.httpResponse.filter(r => matchProperty ? query.test(r[matchProperty]) : query.test(r))
      );
    } else {
      return of([]);
    }
  }

  writeValue(value: any): void {
    this.model = (value && this.typeaheadOptionField) ? value[this.typeaheadOptionField] : value;
  }

  registerOnChange(fn: any): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this._onTouch = fn;
  }

  registerOnValidatorChange(fn: () => void): void {
    this._validatorChange = fn;
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return null;
  }

  public isInvalid(): boolean {
    return this.validationStrategy(this.ngControl);
  }

  public onLoading(isLoading: boolean): void {
    this.isLoading = isLoading;
    this.typeaheadLoading.emit(isLoading);
  }

  public onNoResults(noResults: boolean): void {
    this.hasNoResults = noResults;
    this.typeaheadNoResults.emit(noResults);
  }

  public onBlur(event: TypeaheadMatch): void {
    this.typeaheadOnBlur.emit(event);
  }

  public onInputBlur(): void {
    this._onTouch();
  }

  public onInput(): void {
    this._onChange(undefined);
  }

  public onSelect(event: TypeaheadMatch): void {
    this._onChange(event.item);
    this.typeaheadOnSelect.emit(event);
  }
}
