import {Component, forwardRef, Injector, Renderer2} from '@angular/core';
import {AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors} from '@angular/forms';
import {acertaKeyValidator} from './acerta-key.validator';
import {AcertaKeyFormatter} from 'acerta-datatypes';
import {TextComponent} from '../text/text.component';

@Component({
  selector: 'aui-acerta-key',
  templateUrl: './acerta-key.component.html',
  styleUrls: ['./acerta-key.component.scss'],
  providers: [{
    provide: NG_VALIDATORS,
    useExisting: forwardRef(() => AcertaKeyComponent),
    multi: true,
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => AcertaKeyComponent),
    multi: true,
  }],
})
export class AcertaKeyComponent extends TextComponent {
  constructor(
    renderer: Renderer2,
    injector: Injector,
  ) {
    super(renderer, injector);
  }

  public onInput(): void {
    this.onChange(AcertaKeyFormatter.parse(this.inputField.value));
  }

  public onBlur(): void {
    this.updateView(AcertaKeyFormatter.parse(this.inputField.value));
    this.onTouch();
  }

  updateView(value: string): void {
    if (this.inputField) {
      this.renderer.setProperty(this.inputField, 'value', AcertaKeyFormatter.format(value));
    }
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return acertaKeyValidator(control);
  }
}

