import {Snippet} from '../../shared/snippet/snippet';

export const basicSnippet = Snippet({
  lang: 'html',
  code: `
<aui-datepicker
  id="dp1"
  name="datepicker-1"
  [(ngModel)]="model1">
</aui-datepicker>
`});

export const disabledSnippet = Snippet({
  lang: 'html',
  code: `
<aui-datepicker
          [disabled]="true">
</aui-datepicker>
`});

export const labelSnippet = Snippet({
  lang: 'html',
  code: `
<aui-datepicker
    label="Vul de datum in"
    placeholder="Datum invullen">
</aui-datepicker>
`});

export const requiredSnippet = Snippet({
  lang: 'html',
  code: `
<aui-datepicker
      [isRequired]="true">
</aui-datepicker>
`});

export const minmaxSnippet = Snippet({
  lang: 'html',
  code: `
<aui-datepicker
      [minDate]="selectMinDateModel"
      [maxDate]="selectMaxDateModel">
</aui-datepicker>
`});

export const disabledDaysSnippet = Snippet({
  lang: 'html',
  code: `
<aui-datepicker
      [disabledDates]="list">
</aui-datepicker>
`});

export const pipeSnippet = Snippet({
  lang: 'html',
  code: `
{{readonlyDate | auiDate}}
`});

