import {AfterViewInit, Component, ElementRef, forwardRef, Input, OnChanges, SimpleChanges, ViewChild} from '@angular/core';
import {
  AbstractControl,
  AsyncValidator,
  ControlValueAccessor,
  NG_ASYNC_VALIDATORS,
  NG_VALUE_ACCESSOR,
  NgModel,
  ValidationErrors
} from '@angular/forms';
import {touchedStrategy, ValidationStrategyFn} from '@acerta/ui/common';
import {NgbDate, NgbDateParserFormatter, NgbDatepickerI18n, NgbInputDatepicker} from '@ng-bootstrap/ng-bootstrap';
import {AcertaNgbDateParserFormatter} from './util/acerta-ngb-date.parser.formatter';
import {AcertaNgbDatepickerI18n} from './util/acerta-ngb-datepicker-i18n';
import {isChronological, toDate, toNgbDate} from './util/ngb-date.utils';

@Component({
  selector: 'aui-datepicker',
  templateUrl: './datepicker.component.html',
  styleUrls: ['./datepicker.component.scss'],
  providers: [{
    provide: NG_ASYNC_VALIDATORS,
    useExisting: forwardRef(() => DatepickerComponent),
    multi: true
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => DatepickerComponent),
    multi: true
  }, {
    provide: NgbDateParserFormatter,
    useClass: AcertaNgbDateParserFormatter
  }, {
    provide: NgbDatepickerI18n,
    useClass: AcertaNgbDatepickerI18n
  }]
})
export class DatepickerComponent implements OnChanges, AfterViewInit, ControlValueAccessor, AsyncValidator {
  @ViewChild('dp', { static: true }) datepicker: NgbInputDatepicker;
  @ViewChild('dpControl', { static: true }) dpControl: NgModel;
  @ViewChild('inputFieldRef', { static: true }) inputFieldRef: ElementRef<HTMLInputElement>;

  // todo default values
  @Input() id: string;
  @Input() name: string;
  @Input() placeholder = 'FORMS.DATE.PLACEHOLDER';
  @Input() validationStrategy: ValidationStrategyFn = touchedStrategy;
  @Input() isRequired = false;

  @Input() set minDate(value: Date) {
    this.minNgbDate = !!value ? toNgbDate(value) : null;
    this.minMaxInvalid = !isChronological(this.minNgbDate, this.maxNgbDate);
  }

  @Input() set maxDate(value: Date) {
    this.maxNgbDate = !!value ? toNgbDate(value) : null;
    this.minMaxInvalid = !isChronological(this.minNgbDate, this.maxNgbDate);
  }

  @Input() set disabledDates(value: Date[]) {
    this.disabledNgbDates = !!value ? value.map(d => toNgbDate(d)) : [];
  }

  private _onChange: (value: Date) => {};
  private _onTouch: () => {};
  private _validatorChange: () => void;

  public innerValue: NgbDate;
  public minNgbDate: NgbDate;
  public maxNgbDate: NgbDate;
  public minMaxInvalid = false;
  public disabledNgbDates: NgbDate[] = [];
  public isDisabled = false;

  constructor() {
  }

  ngAfterViewInit(): void {
    this.datepicker.dateSelect.subscribe(() => this._onTouch());
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this._validatorChange) {
      this._validatorChange();
    }
  }

  registerOnChange(fn: any): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this._onTouch = fn;
  }

  registerOnValidatorChange(fn: () => void): void {
    this._validatorChange = fn;
  }

  validate(control: AbstractControl): Promise<ValidationErrors> | null {
    // TODO (technical debt): temporary solution, check alternatives (reactive forms)
    return new Promise((resolve, reject) => {
      setTimeout(() => resolve(this.datepicker.validate(this.dpControl.control)), 0);
    });
  }

  setDisabledState(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  writeValue(value: Date | null): void {
    this.innerValue = toNgbDate(value);
  }

  public onInput(value: NgbDate | string): void {
    if (this._onChange) {
      if (!value || typeof value === 'string') {
        this._onChange(null);
      } else {
        this._onChange(toDate(value));
      }
    }
  }

  public onBlur(): void {
    this._onTouch();
  }

  public isDateDisabled = (date: NgbDate): boolean => this.disabledNgbDates.some(d => d.equals(date));

  public isInvalid(): boolean {
    return this.validationStrategy(this.dpControl);
  }
}
