import {AfterViewInit, Component, OnInit, QueryList, ViewChild, ViewChildren} from '@angular/core';
import {WizardComponent, WizardItemModel, WizardItemValidityStateEnum} from '@acerta/ui';
import {NgForm} from '@angular/forms';

@Component({
  selector: 'app-demo-wizard',
  templateUrl: './demo-wizard.component.html',
  styleUrls: ['./demo-wizard.component.scss']
})
export class DemoWizardComponent implements OnInit, AfterViewInit {
  @ViewChild('wizard', { static: true }) wizard: WizardComponent;
  @ViewChildren('myForm') myForms: QueryList<NgForm>;

  public wizardItems: WizardItemModel[] = [];
  public wizardFormsInputs: string[] = [];

  public wizardItemsLooks: WizardItemModel[] = [];

  constructor() { }

  ngOnInit() {
    this.wizardItems = [
      {
        active: true,
        validityState: WizardItemValidityStateEnum.UNDETERMINED,
        disabled: false,
        label: '<b>Stap 1:</b> Een eerste stap.'
      },
      {
        active: false,
        validityState: WizardItemValidityStateEnum.UNDETERMINED,
        disabled: false,
        label: '<b>Stap 2:</b> Een tweede stap.'
      }, {
        active: false,
        validityState: WizardItemValidityStateEnum.UNDETERMINED,
        disabled: false,
        label: '<b>Stap 3:</b> Een derde stap.'
      }, {
        active: false,
        validityState: WizardItemValidityStateEnum.UNDETERMINED,
        disabled: false,
        label: '<b>Stap 4:</b> Een resulterende stap.'
      }, {
        active: false,
        validityState: WizardItemValidityStateEnum.UNDETERMINED,
        disabled: false,
        label: '<b>Stap 5:</b> Een finale stap.'
      }
    ];
    this.wizardItemsLooks = [
      {
        active: true,
        validityState: WizardItemValidityStateEnum.UNDETERMINED,
        disabled: false,
        label: 'Actief, Undetermined'
      }, {
        active: true,
        validityState: WizardItemValidityStateEnum.VALID,
        disabled: false,
        label: 'Actief, Valid'
      }, {
        active: true,
        validityState: WizardItemValidityStateEnum.INVALID,
        disabled: false,
        label: 'Actief, Invalid'
      }, {
        active: false,
        validityState: WizardItemValidityStateEnum.UNDETERMINED,
        disabled: false,
        label: 'Niet actief, Undetermined'
      }, {
        active: false,
        validityState: WizardItemValidityStateEnum.VALID,
        disabled: false,
        label: 'Niet actief, Valid'
      }, {
        active: false,
        validityState: WizardItemValidityStateEnum.INVALID,
        disabled: false,
        label: 'Niet actief, Invalid'
      }, {
        active: false,
        validityState: WizardItemValidityStateEnum.UNDETERMINED,
        disabled: true,
        label: 'Disabled, Undetermined'
      }, {
        active: false,
        validityState: WizardItemValidityStateEnum.VALID,
        disabled: true,
        label: 'Disabled, Valid'
      }, {
        active: false,
        validityState: WizardItemValidityStateEnum.INVALID,
        disabled: true,
        label: 'Disabled, Invalid'
      }
    ];
  }

  ngAfterViewInit(): void {
    this.myForms.forEach(form => {
      form.valueChanges.subscribe(() => {
        if (form.dirty) {
          this.wizardItems
            .find(wizardItem => wizardItem.active)
            .validityState = form.valid ? WizardItemValidityStateEnum.VALID : WizardItemValidityStateEnum.INVALID;
        }
      });
    });
  }

  onWizardItemClick(aWizardItem: WizardItemModel) {
    this.wizard.goTo(aWizardItem);
  }

  next() {
    this.wizard.goToNext();
  }

  previous() {
    this.wizard.goToPrevious();
  }
}
