import {Component, forwardRef, Injector, Renderer2} from '@angular/core';
import {AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors} from '@angular/forms';
import {TextComponent} from '../text/text.component';
import {emailValidator} from './email.validator';

@Component({
  selector: 'aui-email',
  templateUrl: './email.component.html',
  styleUrls: ['./email.component.scss'],
  providers: [{
    provide: NG_VALIDATORS,
    useExisting: forwardRef(() => EmailComponent),
    multi: true
  }, {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => EmailComponent),
    multi: true
  }]
})
export class EmailComponent extends TextComponent {
  constructor(renderer: Renderer2, injector: Injector) {
    super(renderer, injector);
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return emailValidator(control);
  }

}
