import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {TranslateModule} from '@ngx-translate/core';
import {TooltipModule} from 'ngx-bootstrap/tooltip';
import {CodInitResolve} from '@acerta/ui';
import {MenuComponent} from './menu/menu.component';
import {DemoComponent} from './demo.component';
import {DemoApiComponent} from './demo-api/demo-api.component';
import {ButtonsComponent} from './buttons/buttons.component';
import {FormsComponent} from './forms/forms.component';
import {GridsComponent} from './grids/grids.component';
import {HeaderComponent} from './header/header.component';
import {SharedModule} from '../shared/shared.module';
import {ModalsComponent} from './modals/modals.component';
import {BusinessTypesComponent} from './business-types/business-types.component';
import {AutocompleteComponent} from './autocomplete/autocomplete.component';
import {DatepickerComponent} from './datepicker/datepicker.component';
import {NotificationsComponent} from './notifications/notifications.component';
import {SpinnerComponent} from './spinner/spinner.component';
import {TooltipComponent} from './tooltip/tooltip.component';
import {CodComponent} from './cod/cod.component';
import {IamComponent} from './iam/iam.component';
import {LemonGuard} from './permissions/example/lemon.guard';
import {PermissionsComponent} from './permissions/permissions.component';
import {SecuredComponent} from './permissions/example/secured/secured.component';
import {UnauthorizedComponent} from './permissions/example/unauthorized/unauthorized.component';
import {DemoWizardComponent} from './wizard/demo-wizard.component';
import {TabsComponent} from './tabs/tabs.component';
import { ContainersComponent } from './containers/containers.component';
import { CoinComponent } from './coin/coin.component';

const routes: Routes = [
  {
    path: '',
    component: DemoComponent,
    children: [
      {path: 'datatypes', component: BusinessTypesComponent},
      {path: 'autocomplete', component: AutocompleteComponent},
      {path: 'buttons', component: ButtonsComponent},
      {path: 'demo-api', component: DemoApiComponent},
      {path: 'forms', component: FormsComponent},
      {path: 'grids', component: GridsComponent},
      {path: 'header', component: HeaderComponent},
      {path: 'modals', component: ModalsComponent},
      {path: 'notifications', component: NotificationsComponent},
      {path: 'datepicker', component: DatepickerComponent},
      {path: 'spinner', component: SpinnerComponent},
      {path: 'tabs', component: TabsComponent},
      {path: 'tooltip', component: TooltipComponent},
      {path: 'iam', component: IamComponent},
      {path: 'wizard', component: DemoWizardComponent},
      {path: 'coin', component: CoinComponent},
      {
        path: 'cod',
        component: CodComponent,
        resolve: [CodInitResolve],
        data: {
          cod: {
            definitions: ['B71_Locatie', 'BX3_CuroNationality'],
          },
        },
      },
      {
        path: 'permissions',
        children: [
          {
            path: '',
            component: PermissionsComponent,
          },
          {
            path: 'secured',
            component: SecuredComponent,
            canActivate: [LemonGuard]
          },
          {
            path: 'unauthorized',
            component: UnauthorizedComponent
          },
        ],
      },
      {path: 'containers', component: ContainersComponent},
      {path: '', pathMatch: 'full', redirectTo: 'forms'},
    ]
  },
];

@NgModule({
  declarations: [
    MenuComponent,
    DemoComponent,
    DemoApiComponent,
    ButtonsComponent,
    FormsComponent,
    HeaderComponent,
    ModalsComponent,
    BusinessTypesComponent,
    NotificationsComponent,
    CodComponent,
    DatepickerComponent,
    SpinnerComponent,
    TooltipComponent,
    IamComponent,
    AutocompleteComponent,
    PermissionsComponent,
    SecuredComponent,
    UnauthorizedComponent,
    GridsComponent,
    DemoWizardComponent,
    TabsComponent,
    ContainersComponent,
    CoinComponent,
  ],
  imports: [
    SharedModule,
    TooltipModule,
    RouterModule.forChild(routes),
    TranslateModule.forChild()
  ]
})
export class DemoModule {
}
