import {Injectable} from '@angular/core';
import {isNumber, padNumber, toInteger} from '@acerta/ui/common';
import {NgbDateParserFormatter, NgbDateStruct} from '@ng-bootstrap/ng-bootstrap';

// https://seegatesite.com/tutorial-to-change-date-format-ng-bootstrap-datepicker-angular-5/

@Injectable()
export class AcertaNgbDateParserFormatter extends NgbDateParserFormatter {
    parse(value: string): NgbDateStruct {
        if (value) {
            let dateParts = value.trim().split(/[\/\-\.\ ]/);
            if (dateParts.length === 1 && dateParts[0].length === 8) {
                dateParts = [dateParts[0].substring(0, 2), dateParts[0].substring(2, 4), dateParts[0].substring(4, 8)];
            }
            if (dateParts.length === 1 && isNumber(dateParts[0])) {
                return {
                    day: toInteger(dateParts[0]),
                    month: null,
                    year: null
                };
            } else if (dateParts.length === 2 && isNumber(dateParts[0]) && isNumber(dateParts[1])) {
                return {
                    day: toInteger(dateParts[0]),
                    month: toInteger(dateParts[1]),
                    year: null
                };
            } else if (dateParts.length === 3 && isNumber(dateParts[0])
                && isNumber(dateParts[1]) && isNumber(dateParts[2])) {
                return {
                    day: toInteger(dateParts[0]),
                    month: toInteger(dateParts[1]),
                    year: toInteger(dateParts[2])
                };
            }
        }
        return null;
    }

    format(date: NgbDateStruct): string {
        return date ? `${this.pad(date.day)}/${this.pad(date.month)}/${date.year}` : '';
    }

    private pad(part: number): string {
        return isNumber(part) ? padNumber(part) : '';
    }
}
